        vm.dataArchivoInforma = AtencionDenuncia.modelArchivoAtencion();
        vm.archivoAdjunto = [];
        vm.mimeTypes = [];
        vm.extension = "";
        vm.tamanioArchivo = "500";
        vm.caracteresMaximoArchivo = 50;
        /*********************************/
        /* vm.dtFechaInicioOptions = {
             minDate: new Date,
             showWeeks: false
         };*/
        vm.usuario = {};
