    	vm.prmPedido.fechaTermino =convertDateToString(vm.fechaTerminoPedido);
    	
    	if(vm.prmPedido.fechaInicio==''|| vm.prmPedido.fechaInicio ==null ){
    		vm.prmPedido.fechaInicio ='01/01/1900';
    		
    	}
    	
