        vm.provincia = UbigeoFactory.provincia();
        vm.distrito = UbigeoFactory.distrito();
        vm.listaDepartamento = [];
        vm.listaProvincia = [];
        vm.listaDistrito = [];
        /***************Fin de Variables Ubigeo********************/
        /********************************/
        vm.verHistorialDenunciante = verHistorialDenunciante;
