(function() {
    'use strict';
    angular.module('spapp').factory('CookiesFactory', CookiesFactory);
    /* @ngInject */
    function CookiesFactory($q, $http, $rootScope, $cookies, $state, API_CONFIG, $window) {
        var factory = {
            generarTiempoSession: generarTiempoSession,
            crearCookie: crearCookie,
            obtenerCookie: obtenerCookie,
            aumentarTiempoCookie: aumentarTiempoCookie,
            obtenerObjCookie: obtenerObjCookie,
            validaCookie: validaCookie,
            borrarDatosCookieLocalStorage: borrarDatosCookieLocalStorage
        };
        return factory;
        /*****************************************************************************************/
        function generarTiempoSession() {
            var today = new Date();
            var expiresValue = new Date(today);
            expiresValue.setMinutes(today.getMinutes() + 20);
            return expiresValue;
        }

        function crearCookie(prmRef) {
            var cookieSesSinaWeb = {
                ref: prmRef,
                tiempoSession: generarTiempoSession()
            };
            var data = angular.toJson(cookieSesSinaWeb);
            $cookies.put('cookieSesSinaWeb', data, {
                'expires': generarTiempoSession()
            });
        }

        function aumentarTiempoCookie() {
            if (angular.isDefined($cookies.get('cookieSesSinaWeb'))) {
                var obj = $cookies.get('cookieSesSinaWeb');
                var data = angular.fromJson(obj);
                crearCookie(data.ref);
            }
        }

        function obtenerCookie() {
            var dataRef = null;
            if (angular.isDefined($cookies.get('cookieSesSinaWeb'))) {
                var obj = $cookies.get('cookieSesSinaWeb');
                var data = angular.fromJson(obj);
                crearCookie(data.ref);
                dataRef = data.ref;
            } else {
                $state.go('ingreso');
            }
            return dataRef;
        }

        function obtenerObjCookie() {
            debugger;
            var dataRef = null;
            if (angular.isDefined($cookies.get('cookieSesSinaWeb'))) {
                var obj = $cookies.get('cookieSesSinaWeb');
                var data = angular.fromJson(obj);
                dataRef = data;
            }
            return dataRef;
        }

        function validaCookie() {
            console.log('paso --- > Cokkie');
            if (!angular.isDefined($cookies.get('cookieSesSinaWeb'))) {
                sessionStorage.removeItem('objSesSinadaWebRefRD');
                localStorage.removeItem('oSuSinWebDataSys');
                localStorage.removeItem('dataObjUsuIdEncrypSinadaWeb');
                $window.location.href = API_CONFIG.urlSSO + "/Home/Index?wa=wsignout1.0";
                $state.go('ingreso');
            }
        }

        function borrarDatosCookieLocalStorage() {
            if (!angular.isDefined($cookies.get('cookieSesSinaWeb'))) {
                sessionStorage.removeItem('objSesSinadaWebRefRD');
                localStorage.removeItem('oSuSinWebDataSys');
                localStorage.removeItem('dataObjUsuIdEncrypSinadaWeb');
            }
        }
    }
})();
(function() {
	'use strict';

	angular
		.module('spapp')
		.factory('ConvertFactory', ConvertFactory);

	/* @ngInject */
	function ConvertFactory() {
		
		var factory = {
			toDate : toDate,
			toString : toString,
			toStringDate : toStringDate,
			toTime : toTime,
			isNotNull : isNotNull,
			isNotEmpty : isNotEmpty,
			isNotNoE : isNotNoE
		};
		
		return factory;
		
		function toDate(prmStringDate) {
			if (angular.isDefined(prmStringDate) && prmStringDate!=null && prmStringDate!='') {	
				try{
					var dateArray = prmStringDate.split('/');
					var date = new Date(dateArray[2]+'/'+dateArray[1]+'/'+dateArray[0]);
					return date;
				}
				catch(e){
					return null;
				}
			}
		}

		function toTime(prmStringTime) {
			var result = false, m;
		    var regEx = /^\s*([01]?\d|2[0-3]):?([0-5]\d)\s*$/;
		    if ((m = prmStringTime.match(regEx))) {
		        result = (m[1].length === 2 ? "" : "0") + m[1] + ":" + m[2];
		    }
		    return result;
		}


		function toStringDate(prmObject) {
			
			if(isNotNoE(prmObject)){
				return moment(new Date(prmObject)).format('DD/MM/YYYY');
			}
			return null;
		}

		function toString(prmObject) {
			
			if(angular.isDate(prmObject)){
				return moment(prmObject).format('DD/MM/YYYY');
			}
			return null;
		}


		/*validaciones*/

		function isNotNull(obj) {
			return obj != null;
		}

		function isNotDefined(obj) {
			return obj != undefined;
		}

		function isNotEmpty(obj) {
			return obj.toString().length > 0;
		}

		function isNotNoE(obj) {
			return  isNotNull(obj) && isNotDefined(obj) && isNotEmpty(obj);
		}

	}	
})();
(function() {
    'use strict';
    angular.module('spapp').factory('APIFactory', APIFactory);
    /* @ngInject */
    function APIFactory($q, $http, $rootScope, CookiesFactory) {
        var factory = {
            listar: listar,
            registrar: registrar,
            obtener: obtener,
            actualizar: actualizar,
            eliminar: eliminar,
            consultaPOST: consultaPOST,
            guardarArchivo: guardarArchivo,
            download: download
        };
        return factory;

        function listar(url, objeto) {
            CookiesFactory.aumentarTiempoCookie();
            $rootScope.cargandoInformacion++;
            //$rootScope.$broadcast('aumentarContadorCargando', null);
            var deferred = $q.defer();
            var parametros = undefined;
            if (angular.isDefined(objeto)) {
                parametros = {
                    prmCriterio: angular.toJson(objeto)
                };
            }
            $http({
                url: url,
                method: 'GET',
                params: parametros
            }).success(function(response) {
                deferred.resolve(response);
                $rootScope.cargandoInformacion--;
            }).error(function(error) {
                deferred.reject(error);
                $rootScope.cargandoInformacion--;
            });
            //$rootScope.$broadcast('disminuirContadorCargando', null);
            return deferred.promise;
        }

        function registrar(url, objeto) {
            CookiesFactory.aumentarTiempoCookie();
            if (angular.isDefined(localStorage.dataObjUsuIdEncrypSinadaWeb)) {
                objeto.prm1 = angular.copy(localStorage.dataObjUsuIdEncrypSinadaWeb);
            }
            $rootScope.cargandoInformacion++;
            var deferred = $q.defer();
            $http({
                url: url,
                method: 'POST',
                data: objeto
            }).success(function(response) {
                deferred.resolve(response);
                $rootScope.cargandoInformacion--;
            }).error(function(error) {
                deferred.reject(error);
                $rootScope.cargandoInformacion--;
            });
            //$rootScope.cargandoInformacion--;
            return deferred.promise;
        }

        function obtener(url, id) {
            CookiesFactory.aumentarTiempoCookie();
            $rootScope.cargandoInformacion++;
            var deferred = $q.defer();
            $http({
                url: url + (id ? id : ''),
                method: 'GET'
            }).success(function(response) {
                deferred.resolve(response);
                $rootScope.cargandoInformacion--;
            }).error(function(error) {
                deferred.reject(error);
                $rootScope.cargandoInformacion--;
            });
            //$rootScope.cargandoInformacion--;
            return deferred.promise;
        }

        function actualizar(url, objeto) {
            CookiesFactory.aumentarTiempoCookie();
            if (angular.isDefined(localStorage.dataObjUsuIdEncrypSinadaWeb)) {
                objeto.prm1 = angular.copy(localStorage.dataObjUsuIdEncrypSinadaWeb);
            }
            $rootScope.cargandoInformacion++;
            var deferred = $q.defer();
            $http({
                url: url,
                method: 'POST',
                data: objeto
            }).success(function(response) {
                deferred.resolve(response);
                $rootScope.cargandoInformacion--;
            }).error(function(error) {
                deferred.reject(error);
                $rootScope.cargandoInformacion--;
            });
            //$rootScope.cargandoInformacion--;
            return deferred.promise;
        }

        function eliminar(url, objeto) {
            CookiesFactory.aumentarTiempoCookie();
            if (angular.isDefined(localStorage.dataObjUsuIdEncrypSinadaWeb)) {
                objeto.prm1 = angular.copy(localStorage.dataObjUsuIdEncrypSinadaWeb);
            }
            $rootScope.cargandoInformacion++;
            var deferred = $q.defer();
            $http({
                url: url,
                method: 'POST',
                data: objeto
            }).success(function(response) {
                deferred.resolve(response);
                $rootScope.cargandoInformacion--;
            }).error(function(error) {
                deferred.reject(error);
                $rootScope.cargandoInformacion--;
            });
            return deferred.promise;
        }

        function consultaPOST(url, objeto) {
            CookiesFactory.aumentarTiempoCookie();
            $rootScope.cargandoInformacion++;
            var deferred = $q.defer();
            $http({
                url: url,
                method: 'POST',
                data: objeto
            }).success(function(response) {
                deferred.resolve(response);
                $rootScope.cargandoInformacion--;
            }).error(function(error) {
                deferred.reject(error);
                $rootScope.cargandoInformacion--;
            });
            return deferred.promise;
        }

        function guardarArchivo(url, data) {
            CookiesFactory.aumentarTiempoCookie();
            // debugger;
            if (angular.isDefined(localStorage.dataObjUsuIdEncrypSinadaWeb)) {
                var dataForm = data.getAll("strContenido");
                var dataObj = angular.copy(angular.fromJson(dataForm[0]));
                dataObj.prm1 = angular.copy(localStorage.dataObjUsuIdEncrypSinadaWeb);
                data.delete("strContenido");
                data.append("strContenido", angular.toJson(dataObj));
            }
            $rootScope.cargandoInformacion++;
            var deferred = $q.defer();
            $http.post(url, data, {
                headers: {
                    "Content-type": undefined
                },
                transformRequest: angular.identity
            }).success(function(response) {
                deferred.resolve(response);
                $rootScope.cargandoInformacion--;
            }).error(function(error) {
                deferred.reject(error);
                $rootScope.cargandoInformacion--;
            });
            return deferred.promise;
        }

        function download(url, objeto) {
            CookiesFactory.aumentarTiempoCookie();
            $rootScope.cargandoInformacion++;
            var deferred = $q.defer();
            $http.post(url, objeto, {
                responseType: 'arraybuffer'
            }).success(function(response) {
                deferred.resolve(response);
                $rootScope.cargandoInformacion--;
            }).error(function(error) {
                deferred.reject(error);
                $rootScope.cargandoInformacion--;
            });
            return deferred.promise;
        }
    }
})();
(function() {
	'use strict';

	angular
		.module('spapp')
		.filter('zeroPadding', zeroPaddingFilter);

	/* @ngInject */
	function zeroPaddingFilter($filter) {
		var filter = function (input, length) {
			//return $filter('number')(input * 100, decimals) + ' %';
			var num = parseInt(input, 10);
            length = parseInt(length, 10);
            if (isNaN(num) || isNaN(length)) {
                return input;
            }
            num = '' + num;
            while (num.length < length) {
                num = '0' + num;
            }
            return num;
		}
		return filter;
	}
})();
